export function formatString (string) {
  let formatString = string.toLowerCase()
  formatString = formatString.replace(/[]/g, 'e')
  formatString = formatString.replace(/[]/g, 'a')
  formatString = formatString.replace(/[]/g, 'u')
  formatString = formatString.replace(/[]/g, 'i')
  formatString = formatString.replace(/[]/g, 'c')

  return formatString
}
